import React, { Component } from 'react'

const ErrorComponent = ({ error }) => {
    if (!error) {
        throw new Error('error occurred: props.error is not defined!')
    }
    return <div>error: {error}</div>
}

export default class Counter extends Component {
    constructor(props) {
        console.log('constructor!')
        super(props)
        this.state = {
            counter: 0,
            seed: 0,
            hasError: false
        }
    }

    increment = () => {
        this.setState({ counter: this.state.counter + 1 })
    }

    decrement = () => {
        this.setState({ counter: this.state.counter - 1 })
    }
    componentDidMount() {
        console.log('Component did mount')
        console.log('------')
    }

    componentDidUpdate(prevProps, prevStae) {
        console.log('Component did update')
        console.log('------')
    }

    componentWillUnmount() {
        console.log('Component will un-mount')
        console.log('------')
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.ignoreProp &&
            this.props.ignoreProp !== nextProps.ignoreProp) {
            console.log('Should component update - DO NOT RENDER!')
            console.log('------')
            return false
        }
        console.log('Should component update - RENDER!')
        console.log('------')
        return true
    }

    getSnapshotBeforeUpdate(prevProps, prevState) {
        console.log('Get snapshot before update')
        console.log('------')
        return null
    }

    static getDerivedStateFromProps(props, state) {
        console.log('Get derived state from props', props)
        console.log('------')
        if (props.seed && props.seed !== state.seed) {
            return {
                counter: props.seed,
                seed: props.seed
            }
        }
        return null
    }

    componentDidCatch(error, info) {
        console.log('Component did catch! ')
        console.log('------')
        this.setState({ hasError: true })
    }

    render() {
        console.log('Render! ')
        console.log('------')
        return (
            <div style={{ width: 400, margin: '100px auto', border: '1px solid #eee' }}>
                <div>
                    <button onClick={this.increment}>Increment</button>
                    <button onClick={this.decrement}>Decrement</button>
                </div>
                <div>
                    Counter: {this.state.counter}
                </div>
                {this.props.showErrorComponent && <ErrorComponent />}
            </div>
        )
    }
}
