import React, { Component } from 'react'
import Counter from './counter'
import './app.css'

export class App extends Component {

    constructor(props) {
        super(props)
        this.state = {
            mount: false,
            ignoreProp: 0,
            seed: 0,
            showErrorComponent: false
        }
    }

    mountCounter = () => this.setState({ mount: true })
    unMountCounter = () => this.setState({ mount: false })
    ignoreProp = () => this.setState({ ignoreProp: parseInt(Math.random() * 100) })
    seedGenerator = () => this.setState({ seed: parseInt(Math.random() * 100) })

    componentDidCatch(error, info) {
        console.log('Component did catch! ')
        console.log('------')
        this.setState({ hasError: true })
    }

    showErrorComponent = () => {
        this.setState({ showErrorComponent: true })
    }

    render() {
        if (this.state.hasError) {
            return (
                <div className="flex-container">
                    <div className="flex-row">
                        <div className="flex-item">Oops! Something went wrong.</div>
                    </div>
                </div>
            )
        }

        return (
            <div style={{ width: 400, margin: '100px auto', border: '1px solid #eee', textAlign: 'center' }}>
                <div>
                    <button onClick={this.mountCounter} disabled={this.state.mount}>Mount</button>
                    <button onClick={this.unMountCounter} disabled={!this.state.mount}>UnMount</button>
                    <button onClick={this.ignoreProp} disabled={!this.state.mount}>Ignore Prop</button>
                    <button onClick={this.seedGenerator} disabled={!this.state.mount}>Seed Generator: {this.state.seed}</button>
                    <button onClick={this.showErrorComponent} disabled={!this.state.mount}>Show Error</button>
                </div>
                <div>
                    {this.state.mount ?
                        <Counter
                            showErrorComponent={this.state.showErrorComponent}
                            ignoreProp={this.state.ignoreProp}
                            seed={this.state.seed}
                        /> :
                        null
                    }
                </div>
            </div>
        )
    }
}

export default App
